import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Ad } from '@core/models';
import { ButtonType } from '@shared/components/button/button.component';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';

@Component({
    selector: 'findqo-remove-favorites-dialog',
    templateUrl: './remove-favorites-dialog.component.html',
    styleUrls: ['./remove-favorites-dialog.component.scss'],
    standalone: false
})
export class RemoveFavoritesDialogComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public readonly ad: Ad,
		private readonly dialogRef: MatDialogRef<RemoveFavoritesDialogComponent>,
		private readonly sharedAdsFacade: SharedAdsFacade
	) {}

	readonly buttonType = ButtonType;

	onDismiss(): void {
		this.dialogRef.close(false);
	}

	onDeleteFavoriesAd(ad: Ad): void {
		this.sharedAdsFacade.deleteFavouriteAdFromList(ad, this.dialogRef);
	}
}
