import { Component, Input, OnInit, inject } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from '@core/data-access/services/auth.service';
import { AmplitudeEvent, IconType, PostLoginAction, SellerType, StorageType } from '@core/enums';
import { CommonFacade } from '@core/facades/common.facade';
import { Ad, ClientUser } from '@core/models';
import { Translations } from '@core/models/translations.model';
import { BrowserUtility } from '@core/services/browser-utility.service';
import { EnquiryService } from '@core/services/enquiry.service';
import { IconService } from '@core/services/icon.service';
import { SnackService } from '@core/services/snack.service';
import { ViewportService } from '@core/services/viewport.service';
import { WindowService } from '@core/services/window.service';
import { environment } from '@environments/environment';
import { RemoveFavoritesDialogComponent } from '@feature/favorites/components/remove-favorites-dialog/remove-favorites-dialog.component';
import { SendInquiryDialogComponent } from '@feature/general/send-inquiry/send-inquiry-dialog.component';
import { ButtonType } from '@shared/components/button/button.component';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { AdsService } from '@shared/services/ads.service';
import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';
import { Observable } from 'rxjs';
import { AdPhotoViewerComponent } from '../../ad-photo-viewer/ad-photo-viewer.component';
import { AdReportDialogComponent } from '../../ad-report-dialog/ad-report-dialog.component';
import { AdShareDialogComponent } from '../../ad-share-dialog/ad-share-dialog.component';

@Component({
    template: '',
    standalone: false
})
export abstract class AbstractAdItemComponent implements OnInit {
	public readonly matDialog = inject(MatDialog);
	public readonly adsService = inject(AdsService);
	public readonly router = inject(Router);
	public readonly auth = inject(AuthService);
	public readonly route = inject(ActivatedRoute);
	public readonly viewport = inject(ViewportService);
	public readonly icon = inject(IconService);
	public readonly snackbarService = inject(SnackService);
	private readonly commonFacade = inject(CommonFacade);
	private readonly sharedAdsFacade = inject(SharedAdsFacade);
	private readonly window = inject(WindowService);
	private readonly utility = inject(BrowserUtility);
	private readonly enquiryService = inject(EnquiryService);

	@Input() translations: Translations;
	@Input() ad: Ad;
	@Input() section?: string;
	@Input() isPublicPage = false;
	@Input() isAgentHub = false;

	user$: Observable<ClientUser>;
	datePosted: string;
	showFavoritesIcon = false;
	showSellerInfoForBusinessAds = false;

	readonly iconType = IconType;
	readonly buttonType = ButtonType;
	readonly isSellerModuleEnabled = environment.isSellerModuleEnabled;
	readonly sellerType = SellerType;

	// TODO: Remove this code if it's not being used.
	onReportAd(): void {
		if (this.auth.user()) {
			this.openReportDialog();
		} else {
			this.auth.openAuthDialog({ onLogin: this.openReportDialog.bind(this) });
		}
	}

	openReportDialog(): void {
		this.matDialog.open(AdReportDialogComponent, {
			width: '343px',
			height: 'auto',
			maxWidth: 'unset',
			data: {
				adId: this.ad?.id,
			},
			backdropClass: 'inquiry-backdrop',
		});
	}

	moreOptions(event: Event): void {
		event.preventDefault();
		event.stopPropagation();
	}

	showAllPhotos(): void {
		this.matDialog.open(AdPhotoViewerComponent, {
			width: '100%',
			height: '100%',
			data: {
				ad: this.ad,
				sharedAdsFacade: this.sharedAdsFacade,
			},
			panelClass: 'ad-images',
		});
	}

	onWhatsappMessage(): void {
		this.adsService.onWhatsappMessage(this.ad);
	}

	onCall(): void {
		this.adsService.onPhoneCall(this.ad);
	}

	isFavoritesBtnClicked(ad: Ad, isFavoritesBtnEnable: boolean): void {
		if (ad.isFavorite === '1') {
			if (isFavoritesBtnEnable) {
				this.openRemoveFavoritesDialog(ad);
			} else {
				this.sharedAdsFacade.deleteFavouriteAdFromList(ad);
			}
		} else {
			this.saveFavorites();
		}
	}

	shareAd(): void {
		const TRANSLATIONS = this.commonFacade.getTranslations();
		const AVALABLE_FROM = this.ad.availableFrom ? TRANSLATIONS?.['n3111'] + ': ' + this.commonFacade.getFormattedDate(this.ad.availableFrom) : '';

		if (this.utility.isWebShareSupported()) {
			navigator
				.share({
					title: this.ad?.title,
					text: `${this.ad?.title}\n${TRANSLATIONS?.['n152']}:  ${+this.ad.price.isPriceOnApplication ? TRANSLATIONS?.['n3311'] : this.ad.price.symbol + this.ad.price.minValue + ' ' + (this.ad.price.maxValue ? ' - ' + this.ad.price.maxValue : '')}\n${AVALABLE_FROM}\n${TRANSLATIONS?.['n3953']}\n`,
					url: this.ad?.adlink ?? this.window.reference.location.href,
				})
				.then(() => {
					// TODO: Do we need snackbar here?
					console.log('Content shared successfully!');
				})
				.catch((error) => {
					console.error('Error sharing content:', error);
				});
		} else {
			this.matDialog.open(AdShareDialogComponent, {
				width: '343px',
				data: {
					title: TRANSLATIONS?.['n195'],
					messageHeading: TRANSLATIONS?.['n720'],
					confirmButton: TRANSLATIONS?.['n226'],
					snackbarText: TRANSLATIONS?.['n809'],
					adLink: this.ad.adlink,
				},

				panelClass: 'share-ad',
			});
		}
	}

	getMessage(status: string) {
		if (status === 'sold') {
			return 'This property is sold';
		} else if (status === 'property-occupied') {
			return 'This property is occupied';
		} else if (status === 'sale-agreed') {
			return 'This property is sale agreed';
		} else {
			return '';
		}
	}

	onSendInquiry(status: string): void {
		if (['sold', 'property-occupied', 'sale-agreed', 'expired'].includes(status) && this.viewport.desktop) {
			return null;
		}
		if (['sold', 'property-occupied', 'sale-agreed', 'expired'].includes(status) && !this.viewport.desktop) {
			this.snackbarService.open({
				data: { message: this.getMessage(status) },
				panelClass: 'snackbar-error',
				duration: 3000,
			});
			return;
		}
		this.enquiryService.onOpenSendEnquiryDialog(AmplitudeEvent.AD_DETAILS_SEND_ENQUIRY_CLICKED, () => this.openSendInquiryDialog());
	}

	private addToFav(): void {
		if (this.auth.user()?.id !== this.ad.seller.id) {
			this.ad = {
				...this.ad,
				isFavorite: '1',
			};
			this.sharedAdsFacade.addFavouriteAdFromList(this.ad);
		}
	}

	private saveFavorites(): void {
		const IS_LOGGED_IN = this.auth.user()?.accessToken;
		if (IS_LOGGED_IN) {
			this.addToFav();
		} else {
			sessionStorage.setItem(
				StorageType.POST_LOGIN_SESSION,
				JSON.stringify({
					type: PostLoginAction.ADD_TO_FAVORITE,
					payload: {
						...this.ad,
						isFavorite: '1',
					},
				})
			);
			this.auth.openAuthDialog({ onLogin: this.addToFav.bind(this) });
		}
	}

	private openRemoveFavoritesDialog(ad: Ad): void {
		this.matDialog.open(RemoveFavoritesDialogComponent, {
			maxWidth: '343px',
			width: '100%',
			data: ad,
			panelClass: 'remove-favorites-dialog',
		});
	}

	private openSendInquiryDialog(): void {
		this.matDialog.open(SendInquiryDialogComponent, {
			width: '100%',
			height: '100%',
			position: {
				right: '0',
			},
			data: {
				ad: this.ad,
				sharedAdsFacade: this.sharedAdsFacade,
			},
			panelClass: 'inquiry-form',
			backdropClass: 'inquiry-backdrop',
		});
	}

	private showSellerDetailsForBusinessAds(): void {
		if (this.ad?.seller?.sellerTypeId === '2') {
			this.showSellerInfoForBusinessAds = true;
		}
	}

	ngOnInit(): void {
		dayjs.extend(relativeTime);
		this.datePosted = dayjs(+this.ad?.createdAt * 1000).fromNow();
		this.user$ = this.commonFacade.getUser$();
		this.showSellerDetailsForBusinessAds();
	}
}
