import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { SharedModule } from '@shared/shared.module';
import { ButtonModule } from '@shared/components/button/button.module';
import { Ad, ClientUser } from '@core/models';
import { MatTooltipModule } from '@angular/material/tooltip';
import { AbstractAdItemComponent } from '@shared/components/ads/ad-item/abstracts/ad-item.abstract';
import { AisleType } from '@core/enums';
import { BER_EXEMPT } from '@core/constants';

interface BadgeProperty {
	property: string;
	badgeType: string;
}
@Component({
    selector: 'findqo-ad-actions',
    imports: [ButtonModule, SharedModule, MatTooltipModule],
    templateUrl: './ad-actions.component.html',
    styleUrl: './ad-actions.component.scss'
})
export class AdActionsComponent extends AbstractAdItemComponent implements OnInit {
	constructor(private readonly adsApi: AdsApi) {
		super();
	}
	@Input() override ad: Ad;
	@Input() user: ClientUser;
	readonly berExempt = BER_EXEMPT;
	readonly aisleType = AisleType;
	readonly defaultCountryCode = '+353';
	isPhoneClicked = false;

	showPhoneNumber(ad: Ad, event: Event) {
		event.preventDefault();
		event.stopPropagation();
		this.adsApi.trackPhoneCall(ad?.id).subscribe();
		this.isPhoneClicked = true;
	}

	onCallPhone(ad: Ad, event: Event) {
		event.preventDefault();
		event.stopPropagation(); // Stop event propagation
		if (!this.isPhoneClicked) {
			this.adsApi.trackPhoneCall(ad?.id).subscribe();
		}

		const phoneNumber = `tel:${this.defaultCountryCode}${ad?.contact?.phone}`;
		window.location.href = phoneNumber; // Trigger the phone call programmatically
	}

	override ngOnInit(): void {
		this.isPhoneClicked = false;
		super.ngOnInit();
	}
}
