import { Component, OnInit } from '@angular/core';
import { BER_EXEMPT } from '@core/constants';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { AisleType } from '@core/enums';
import { RouteDataType } from '@core/services/route.service';
import { AbstractAdItemComponent } from '@shared/components/ads/ad-item/abstracts/ad-item.abstract';

@Component({
    selector: 'findqo-ad-item-v2',
    templateUrl: './ad-item-v2.component.html',
    styleUrls: ['./ad-item-v2.component.scss', '../styles/ad-item.component.scss'],
    standalone: false
})
export class AdItemV2Component extends AbstractAdItemComponent implements OnInit {
	constructor(private readonly adsApi: AdsApi) {
		super();
	}
	showSellerInfo = false;
	isFavoritesBtnEnable = false;
	readonly berExempt = BER_EXEMPT;
	isSellerPage = false;
	readonly aisleType = AisleType;
	isPhoneClicked = false;

	checkRouteData(): void {
		this.showSellerInfo = this.route.snapshot.data[RouteDataType.SHOW_SELLER_INFO];
		this.isFavoritesBtnEnable = this.route.snapshot.data[RouteDataType.IS_FAVORITE_BTN_ENABLE];
		this.isSellerPage = this.route.snapshot.data[RouteDataType.IS_SELLER_PAGE];
	}

	override ngOnInit(): void {
		this.isPhoneClicked = false;

		super.ngOnInit();
		this.checkRouteData();
	}
}
